import { useState } from "react";
import styled from "styled-components";
import LoginModal from "./LoginModal";
export const Header = () => {
  const menu = ["Our Company", "The Pictures", "Notice", "Board"];
  const [showLogin, setShowLogin] = useState(false);
  const [login, setLogin] = useState(false)
  return (
    <>
      <Container>
        <TitleWrapper>
          <Logo src="https://cdn2.iconfinder.com/data/icons/free-version/128/recycling-512.png" />
          <Title>에코</Title>
          {login || <BtnLogin onClick={() => setShowLogin(true)}>로그인</BtnLogin>}
          {login && <BtnLogout>로그아웃</BtnLogout>}
          {showLogin && <LoginModal onClose={() => setShowLogin(false)} />}
        </TitleWrapper>
        <Menu>
          <List>
            {menu.map((item) => (
              <ListItem key={item}>{item}</ListItem>
            ))}
          </List>
        </Menu>
      </Container>
    </>
  );
};

const Container = styled.div`
  padding: 20px;
  display: flex;
  align-items: center;
  justify-content: space-around;
  background-color: #54b354;
 
  height: 70px;
`;
const TitleWrapper = styled.div`
  display: flex;
  align-items: center;
`;
const Logo = styled.img`
  width: 50px;
  height: 50px;
`;
const Title = styled.h3`
  padding: 10px;
`;
const BtnLogin = styled.button`
  cursor: pointer;
`;
const BtnLogout = styled.button`
  cursor:pointer
  `;
const Menu = styled.nav``;
const List = styled.ul`
  display: flex;
  list-style: none;
`;
const ListItem = styled.li`
  cursor: pointer;
  & + & {
    padding-left: 1rem;
  }
`;

export default Header;
